.Menu {
	background:  var(--main-color);
	width: 0%;
	opacity: 0;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	-webkit-box-shadow: 2px 0px 10px rgba(51,51,51, 0.6);
	        box-shadow: 2px 0px 10px rgba(51,51,51, 0.6);
	z-index: 100;
}

.openMenu {
	width: 25%;
	opacity: 1;
	z-index: 100;
}

.MenuHeader {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	background:  var(--main-shade);
	height: 100px;
	-webkit-box-shadow: 0px 4px 10px rgba(51,51,51, 0.6);
	        box-shadow: 0px 4px 10px rgba(51,51,51, 0.6);
	font-size: 2.4em;
	color: white;

}

.closeMenuIcon {
}

.MenuTabList {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: calc(100% - 100px);
}


